@import "../../../variables.scss";
@import "../../../mixins.scss";

.navigationbar {
  &__wrapper {
    background-color: #fdfaf5;
    position: relative;
    justify-content: flex-start;
    width: 100%;
    padding: 0 80px;
    @include for-phone-only {
      padding: 0 30px;
    }

    .links-wrapper {
      display: flex;
      @include for-phone-only {
        display: none;
        &.visible {
          z-index: 100;
          display: flex;
          flex-direction: column;
          position: absolute;
          background-color: #fff;
          width: 90%;
          top: 85px;
          left: 5%;
          border: 1px solid $base-color;
          border-radius: 15px;
          .navigationbar__box-item {
            padding: 20px;
            border-bottom: 1px solid #9a1f56;
          }
        }
      }
    }
  }
  &__wrapper-nav {
    display: flex;
    justify-content: space-between;
  }
  &__nav {
    padding: 30px 0 0 60px;
    &-date {
      margin-bottom: 20px;
      @include for-phone-only {
        margin-bottom: 8px;
      }
    }
    &-right {
      padding-top: 40px;
      @include for-phone-only {
        padding-top: 20px;
        padding-bottom: 20px;
      }
    }
    &-calendar,
    &-time,
    &-address {
      width: 35px;
      height: 35px;
      margin-right: 20px;
      @include for-phone-only {
        width: 25px;
        height: 25px;
        margin-right: 10px;
      }
    }
  }

  &__logo {
    width: 70px;
    height: 70px;
    margin-top: 20px;
    margin-right: 30px;
    @include for-phone-only {
      width: 45px;
      height: 45px;
    }
  }
  &__box {
    display: flex;
    &-item {
      padding-top: 40px;
      padding-right: 90px;
      a {
        color: #000;
        &:hover {
          color: #cd00cd;
          padding-bottom: 3px;
          border-bottom: 1px solid $color-shadowy;
        }
      }
    }
  }
  &__wrapper-buttonlng {
    position: absolute;
    top: 3px;
    right: 5px;
    @include for-phone-only {
      top: 5px;
      right: 10px;
    }
  }
  &__button-lng {
    border-radius: 7px;
    padding: 8px 12px;
    color: $color-white;
    background-color: #cd00cd;
  }
  &__button-mobile {
    display: none;
    svg {
      stroke: #cd00cd;
      fill: #cd00cd;
    }
    @include for-phone-only {
      display: inline;
      background-color: transparent;
    }
  }
}
.text-normal {
  @include for-phone-only {
    font-weight: 500;
    font-size: 11px;
    line-height: 10px;
    letter-spacing: 0;
    padding-left: 0;
  }
}
